<template>
	<view style="group">
		<view class="section" style="">
			<view class="tip">
				<text class="">聊天成员</text>
			</view>
			<view class="member-item" style="">
				<view class="list" @click="enterMember(item.id)" v-for="(item,index) in memberList" :key='index'>
					<view class="image" style="">
						<u-image width="100%" height="80rpx" :src="item.avatarLink" mode="aspectFit">
							<view slot="error" style="">
								<u-image width='80rpx' height='80rpx' src='/static/image/person.png' mode="aspectFit"></u-image>
							</view>
						</u-image>
					</view>
					<text class="Membername">{{item.name}}</text>
				</view>
				<view class="list" @click="addOther" style="">
					<u-icon name="plus" size='60' color='#999'></u-icon>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {getGroupMember} from '@/utill/api/connect/connect.js'
	export default{
		data(){
			return{
				userId:uni.getStorageSync('doctorId'),
				memberList:[],
				groupId:''
			}
		},
		methods:{
			// 添加其他人
			addOther(){
				console.log(this.groupId)
				uni.navigateTo({
					url:'/pages/shoyiiChat/inviteOther?groupId='+this.groupId
				})
			},
			// 获取成员
			Getmember(){
				getGroupMember({
					groupId:this.groupId
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.memberList=res.data.object.groupNumber
						uni.setNavigationBarTitle({//显示群组名称
							title:res.data.object.name
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			enterMember(id){
				console.log(id)
			},
		},
		onLoad(options) {
			console.log(options)
			this.groupId=options.groupId
			this.Getmember()
		},
		// onBackPress(){
			
		// }
	}
</script>

<style scoped lang="scss">
	page{
		width: 100%;
		height: 100%;
	}
	.group{
		width: 100%;
		/* #ifdef H5 */
		height:100%;
		/* #endif */
		/* #ifndef H5 */
		height: 100vh;
		/* #endif */
		display: flex;
		flex-direction: column;
		.header{
			padding: 0 30rpx;
		}
	}
	.section{
		flex: 1;
		height: 100%;
		overflow: auto;
		padding: 0 30rpx;background-color: #FFFFFF;
		.tip{
			padding: 30rpx 0;
			color: #000000;
			font-weight: bold;
			font-size: 36rpx;
		}
		.member-item{
			padding: 0 0 20rpx;
			display: flex;flex-wrap: wrap;justify-content: flex-start;
			.list{
				width: 20%;
				display: flex;flex-direction: column;
				align-items: center;justify-content: center;
				padding: 10rpx 0;
				margin: 10rpx 0;
				.image{
					box-shadow:0 0 5px 1px #efefef ;
					border-radius: 6rpx;
					width: 80rpx;height: 80rpx;
				}
				.Membername{
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 1;
					overflow: hidden;
					word-wrap: break-word;
					word-break: break-all;
				}
			}
		}
	}
</style>
